@tailwind base;
@tailwind components;
@tailwind utilities;

 #main{
  background: rgb(238,174,202);
  background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
 }
 #heading{
background: radial-gradient(circle farthest-corner at center center, #cf57bf 27%, #1bccdf 100%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
 }
 .btn{
    background: rgb(238,165,197);
    background: radial-gradient(circle, rgba(238,165,197,1) 0%, rgba(198,161,240,1) 100%);
 }
 #about{
   background: linear-gradient(to right, #C91DCF 0%, #7D46CF 74%);
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
 }
 .text:after{
   content:"";
   animation: changetext 4s infinite linear;
 }
 @keyframes changetext{
   0%{
     content: "Web Designer";
   }
   100%{
     content: "Web Developer";
   }
 }